import {useForm} from "react-hook-form";
import Button from "react-bootstrap/Button";
import React from "react";
import axios from "axios";
import {useNavigate} from "react-router-dom";

import {ipAddress, getCookieValue, tokenName, successToast} from "../../utils/Common";

const EditForm = ({formValues}) => {
  let navigate = useNavigate();
  // console.log(formValues)
  let {register, handleSubmit, formState: {errors}} = useForm({
    defaultValues: formValues
  });

  async function onSubmit(data) {
    // console.log(data)
    try {
      let token = getCookieValue(tokenName);
      let res = await axios.put(ipAddress + "admin/updateCountry", data, {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content_Type': 'application-json',
        }
      })
      console.log(res)

      if(res.data.responseCode == 2)
      {
        successToast("Edit Successfully");
        setTimeout(() => navigate("/adminmanagecountries"), 1500)
      }
      else
      {
        alert("Error while deleting");
      }
    } catch (e) {
      console.log(e)
    }
  }

  return (
    <>
      <form onSubmit={handleSubmit(onSubmit)}>
        <input {...register('CountryId', {required: true})} placeholder="Country Name" type="hidden" id="name"
               className="form-control"/>

        <div className="mb-3">
          <label htmlFor="name">Country Name</label>
          <input {...register('Name', {required: true})} placeholder="Country Name" type="text" id="name"
                 className="form-control"/>

          {errors.Name && <p className='text-danger'>This is a required field.</p>}
        </div>

        <div className="mb-3">
          <label htmlFor="code">Country Code</label>
          <input {...register('Code', {required: true})} placeholder="Country Code" maxLength={5} type="text" id="code"
                 className="form-control"/>

          {errors.Code && <p className='text-danger'>This is a required field.</p>}
        </div>

        <div className="mb-3">
          <label htmlFor="region">Region Code</label>
          <input {...register('RegionCode', {required: true})} placeholder="Region Code" type="text" id="region"
                 className="form-control"/>

          {errors.RegionCode && <p className='text-danger'>This is a required field.</p>}
        </div>

        <div className="mb-3">
          <label htmlFor="currency">Currency Code</label>
          <input {...register('CurrencyCode', {required: true})} placeholder="Currency Code" type="text"
                 id="currency"
                 className="form-control"/>

          {errors.CurrencyCode && <p className='text-danger'>This is a required field.</p>}
        </div>

        <hr/>

        <div className="mb d-flex justify-content-between align-items-center">
          <Button variant="primary" type="submit" className="ml-auto">
            Edit Info
          </Button>
        </div>
      </form>
    </>
  )
}
export default EditForm
