import React, {useEffect, useState} from 'react'
import {ToastContainer} from 'react-toastify';
import {useNavigate, useLocation} from "react-router-dom";

import {AppContent, AppSidebar, AppFooter, AppHeader} from '../components/index'
import {tokenName, checkCookieExists, getCookieValue, ipAddress} from '../utils/Common';
import axios from "axios";
import useSlot from "@mui/material/utils/useSlot";

const DefaultLayout = () => {
  const navigate = useNavigate();
  const location = useLocation();

  const [verified, setVerified] = useState(true);

  async function VerifyToken(token) {
    try {
      let res = await axios.post(ipAddress + "admin/verifyToken", {}, {
        headers: {'Authorization': `Bearer ${token}`}
      });

      if (res.status === 200) {
        if (res.data.error) {
          navigate("/");
        } else {
          setVerified(true)
        }
      }
    } catch (e) {
      navigate("/");
    }
  }

  useEffect(() => {
    let check = checkCookieExists(tokenName); // true / false

    if (!check) {
      navigate("/");
    } else {
      // const token = getCookieValue(tokenName);
      // VerifyToken(token).then();
    }
  }, [location]);

  return (
    <div>
      {verified && <>
        <AppSidebar/>
        <div className="wrapper d-flex flex-column min-vh-100 bg-light">
          <AppHeader/>
          <div className="body flex-grow-1 px-3">
            <AppContent/>
          </div>
          <AppFooter/>
        </div>
        <ToastContainer/>
      </>
      }
    </div>
  )
}

export default DefaultLayout
